.grid1{
    text-align: center;
    display: grid;
    grid-template-columns: auto auto;
    width:70%;
    margin: auto;
    border: solid white;
    box-shadow: 15px 15px 15px 15px gainsboro;
}
.pic{
    width:70%;
    border: solid 2px black;
    margin: 10px;
    border-radius: 15%;
}
.para{
    padding: 20px;
    color: darkblue;
}
.para ul{
    margin: 20px;
}
.para ul li{
    padding: 10px;
    background: rgba(245, 245, 245, 0.667);
}
.grid1 a button{
    color: darkblue;
    font-weight: bold;
    padding: 5px;
    margin: 10px;
    border: dashed darkslateblue 5px;
}
.grid1 a button:hover{
    cursor:grab;
}
.footer{
    background: black;
    text-align: center;
    color: gainsboro;
    padding: 20px;
}

@media only screen and (max-width:720px){
    .grid1{
        text-align: center;
        display: grid;
        grid-template-columns: auto;
        width:100%;
        margin: auto;
    }
    .para ul li{
        padding: 10px;
        background: rgba(245, 245, 245, 0.667);
    }
}