 *{
  margin: 0px;
  box-sizing: border-box;
  background:url('/build/images/portfolioBg.jpg');
  font-size: large;
  color: darkblue;
}
img{max-width: 100%;}

h2{
  text-align: center;
  font-size: 2.5rem;
  background:linear-gradient(to left, pink, rgb(163, 163, 237), lightblue);
  padding:40px;
  color:white;
}

@media only screen and (max-width:720px){
  *{
    font-size:medium;
  }
  h2{
    font-size: 1.75rem;
  }
}