.grid3{
    display: grid;
    grid-template-columns: auto auto auto;
    width :70%;
    margin: auto;
    padding: 50px;
    border: solid white;
    box-shadow: 15px 15px 15px 15px gainsboro;
    text-align: center;
}
.grid3 div a img{
    padding: 10px
}
.sec3 h3{
   text-align: center;
   font-size: 1.25rem;
   margin: 20px;
}
.sec3 ul li{
    padding: 10px;
    text-align: center;
    border:solid 2px lavenderblush;
}
.icons{
    font-size: large;
}

@media only screen and (max-width:720px){
    .grid3{
        display: grid;
        grid-template-columns: auto auto;
        width :100%;
        margin: auto;
    }
    .sec3 ul li{
        padding: 10px;
        text-align: center;
        border:solid 2px lavenderblush;
    }
}