.grid2{
    display: grid;
    grid-template-columns: auto auto auto auto;
    color:darkblue;
    width:70%;
    margin: auto;
    font-weight: bold;
    border: solid white;
    box-shadow: 15px 15px 15px 15px gainsboro;
}
.border{
    font-size: 1.15rem;
    padding: 7px;
    text-align: center;
    margin: 20px;
    background:rgba(211, 211, 211, 0.455);
    border: solid white;
    box-shadow: 15px 15px 15px 15px gainsboro;
}

@media only screen and (max-width:720px){
    .grid2{
        display: grid;
        grid-template-columns: auto auto;
        width:100%;
        margin: auto;
    }
}